/* topBar.module.css */
body {
  font-family: Neue Plak, -apple-system, BlinkMacSystemFont, Roboto,
    Helvetica Neue, Helvetica, Tahoma, Arial, sans-serif;
}
.mainTopBar {
  background-color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 1%;
  opacity: 10;
}

.menuButtons {
  display: flex;
}
.buttonName {
  font-weight: bold;
  cursor: pointer;
  color: #39364f;
  opacity: 1;
  z-index: 1;
  transition: 100ms;
}
.buttonBar {
  display: flex;
  gap: 200px;
  flex-direction: row;
  border-bottom: transparent solid 0.5px;
}
.buttonName:hover {
  border-bottom: #39364f solid 0.5px;
}
.contactButton {
  border-bottom: transparent 0.5px solid;
  color: #39364f;
  cursor: pointer;
  font-weight: bold;
}
.contactButton:hover {
  border-bottom: #39364f 0.5px solid;
}
.linkStyle {
  text-decoration: none;
}
.sideBarStyle {
  position: absolute;
  height: 100vh;
  left: 0;
  top: 0;
  width: 100%;
}
@media only screen and (max-width: 600px) {
  .mainTopBar {
    padding: 1%;
    font-size: 20px;
  }
  .buttonBar {
    gap: 58px;
  }
}
@media only screen and (max-width: 430px) {
  .mainTopBar {
    padding: 1%;
    font-size: 15px;
  }
  .buttonBar {
    gap: 20px;
  }
}
